<template>
  <div>
    <header-title/>
    <h1>{{ title }}</h1>
    <list/>
  </div>
</template>

<script>
  import List from '@/components/trackList.vue'
  import HeaderTitle from '@/components/header.vue'

  export default {
    name: 'Artists',
    components: {List, HeaderTitle},
    data() {
      return {
        title: 'Recently played Top Tracks',
      }
    },
  }
</script>



