<template>
  <header>
    <router-link to="/">{{title}}</router-link>
    <login/>
  </header>
</template>

<script>
import Login from '@/components/login.vue';

export default {
  name: 'HeaderTitle',
  components: {login: Login},
  data() {
    return {
      title: 'music-o-meter'
    }
  },
}
</script>

<style>
  header {
    margin-bottom: 30px;
  }
  header a {
    font-size: 48px;
    font-weight: 700;
    color: #f5f5f5;
    text-decoration: none;
    letter-spacing: 0.09em;
    transition: letter-spacing .3s;
  }

  header a:hover {
    letter-spacing: 0.18em;
  }
</style>



