<template>
    <a class="button button--login" href="/auth/spotify">login</a>
</template>

<script>
export default {
  name: 'Login',
  methods: {
    openPopup () {
      const src = '/auth/spotify'

      const overlay = document.createElement('div')
      overlay.id = 'overlay'
      document.body.appendChild(overlay)

      const iframe = document.createElement('iframe')
      iframe.src = src
      iframe.id = 'loginFrame'
      iframe.name = 'loginFrame'

      overlay.appendChild(iframe)
      overlay.classList.add('visible')
    }
  }
}
</script>

<style>
  .button--login {
    color: #59ef65;
    border-color: #59ef65;
  }
</style>



