<template>
  <div>
    <ul class="filter-list">
      <li v-bind:key="item.id" v-for="item in genres">
        {{item.value}}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Filters',
  props: ['filters'],
  data () {
    return {
      genres: [{id: '', value: ''}]
    }
  },
  mounted () {
    let list = this.filters.map((item, id) => ({
        id,
        value: item
      }))
    console.log(list)
    this.genres = list
  }
}
</script>

<style>
  .filter-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .filter-list li {
    padding: 3px 6px;
    margin: 0 5px 10px;
    border: 1px solid #f5f5f5;
    border-radius: 12px;
  }
</style>



