<template>
  <div>
    <ul>
      <li v-bind:key="item.id" v-for="item in list">
        <h3>{{item.key}}</h3>
        <p>{{item.value}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
  import Spotify from 'spotify-web-api-js'

  let spotifyApi = new Spotify()
  let token = window.localStorage.token

  spotifyApi.setAccessToken(token)

  export default {
    name: 'Info',
    data () {
      return {
        list: [{id: 0, key: '', value: ''}],
        info: {}
      }
    },
    mounted: function() {
      let trackID = this.$route.params.id

      spotifyApi.getAudioFeaturesForTrack(trackID).then((data) => {
        let list = []
        console.log('User playlists', data)
        this.info = data

        this.list = Object.keys(data).map((key, id) => {
          return {
            id,
            key,
            value: data[key]
          }
        })
      }, function (err) {
        console.error(err)
      })
    }
  }
</script>

<style>
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
</style>



