<template>
  <div id="home">
    <header-title/>
    <router-link class="button" to="/top-artists">See my Top Artists</router-link>
    <router-link class="button" to="/top-tracks">See my Top Tracks</router-link>
  </div>
</template>

<script>
import HeaderTitle from './header.vue'

export default {
  name: 'Home',
  components: {HeaderTitle},
  data () {
    return {
      title: 'music-o-meter',
    }
  },
}
</script>

<style>
  #home {
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
  }

  .button {
    display: block;
    max-width: 200px;
    padding: 8px 15px;
    margin-bottom: 15px;
    font-size: 22px;
    color: #f5f5f5;
    border: 1px solid #f5f5f5;
    border-radius: 22px;
    text-decoration: none;
    transition: color .3s, border-color .3s;
  }

  .button:hover {
    color: #EDC2AF;
    border-color: #EDC2AF;
  }
</style>



