<template>
  <ul>
    <li v-bind:key="item.id" v-for="item in list">
      <h1>{{item.value.name}}</h1>
      <h2>{{item.value.artist}}</h2>
      <p>{{item.value.album}}</p>
      <img v-bind:src="item.value.image">
      <router-link v-bind:to="'/track-analysis/' + item.value.track">get track analysis</router-link>
    </li>
  </ul>
</template>

<script>
import Spotify from 'spotify-web-api-js'

let spotifyApi = new Spotify()
let token = window.localStorage.token

spotifyApi.setAccessToken(token)

export default {
  name: 'List',
  data () {
    return {
      list: [{id: 0, value: {name: '', image: '', item: ''}}]
    }
  },
  mounted: function () {
    spotifyApi.getMyTopTracks({time_range: 'medium_term'}).then((data) => {
      let list = []
      console.log('User tracklist', data)
      data.items.forEach((item, id) => {
        list.push({
          id,
          value: {
            name: item.name,
            artist: item.artists[0].name,
            album: item.album.name,
            image: item.album.images[1].url,
            track: item.uri.split(':').pop(),
            item
          },
        })
      })
      this.list = list
    }, function (err) {
      console.error(err)
    })
  },
}
</script>

<style>
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
</style>



