<template>
  <div>
    <filters v-bind:filters="genres"/>
    <ul>
      <li v-bind:key="item.id" v-for="item in list">
        <h1>{{item.value.name}}</h1>
        <img v-bind:src="item.value.image">
        <p>{{item.value.genres}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import Filters from './genreFilters.vue'
import Spotify from 'spotify-web-api-js'

let spotifyApi = new Spotify()
let token = window.localStorage.token

spotifyApi.setAccessToken(token)

export default {
  name: 'List',
  components: {Filters},
  data () {
    return {
      list: [{id: 0, value: {name: '', image: ''}}],
      genres: []
    }
  },
  mounted: function () {
    spotifyApi.getMyTopArtists({time_range: 'medium_term'}).then((data) => {
      let list = []
      let genres = []
      console.log('User playlists', data)
      data.items.map((item, id) => {
        list.push({
          id,
          value: {
            name: item.name,
            image: item.images[1].url,
            genres: item.genres
          }
        })
        item.genres.forEach(genre => {
          genres.push(genre)
        })
      })
      this.list = list
      this.genres = genres.filter((v, i) => {
        return genres.indexOf(v) === i
      }).sort()
    }, function (err) {
      console.error(err)
    })
  },
}
</script>

<style>
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
</style>



